* {
  box-sizing: border-box;
}
body {
  font-family: 'Montserrat', sans-serif;
}
html {
  background-color: rgba(86,61,124,.05);
}

.home_page_section{
  min-height: 100vh;
  background-color: rgb(0 0 0 / 90%);
}

.title{
  font-size: 40px;
  font-weight: 900;
  line-height: 44px;
  letter-spacing: 4px;
  text-transform: uppercase;
}
.border-10{
  border:10px solid #dee2e6!important;
}

.min-height-width{
  min-height: 130px;
}

.disabled{
  pointer-events: none;
  opacity: 0.8;
}

.card_modified{
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-clip: border-box;
    border: 1px solid rgb(255 255 255 / 83%);
    width: 33.33%;
}

.font-big{
    font-size: 50px;
    font-weight: 900;
}